.app {
  font-family: "Roboto Mono", monospace;
  text-align: center;
}

.footer {
  text-align: center;
  margin-top: 8rem;
  padding-bottom: 0.5rem;
}

.footer .link {
  color: black;
  text-decoration: none;
  font-size: smaller;
}

.footer .footer-header {
  font-weight: bold;
  font-size: small;
}

.list-non-bullet {
  list-style: none;
  padding-inline-start: 0px;
  font-size: smaller;
}

.list-item-inline {
  display: inline;
  padding: 0rem 0.5rem;
}
